import React from 'react'
import styles from './Footer.module.css'
import MaterialLink from '@material-ui/core/Link'
import { Link } from 'react-router-dom'
import wmImage from '../../images/wm_persona.png'
import { useTranslate } from '../hooks/useTranslate'

import ListItemText from '@material-ui/core/ListItemText'

function Contacts() {
	return (
		<div style={{ color: '#3c4858' }}>
			<ListItemText
				primary={<span className={styles.font}>Skype</span>}
				secondary={
					<div className={styles.font}>
						<MaterialLink href="skype:wow_gold77">wow_gold77</MaterialLink>
						<br />
						<MaterialLink href="skype:exit291988">exit291988</MaterialLink>
					</div>
				}
			/>
			<ListItemText
				primary={<span className={styles.font}>Telegram</span>}
				secondary={
					<div className={styles.font}>
						<MaterialLink href="tg://resolve?domain=richlegion">@richlegion</MaterialLink>
					</div>
				}
			/>
			<ListItemText
				primary={<span className={styles.font}>Mail</span>}
				secondary={
					<div className={styles.font}>
						<MaterialLink href="mailto:admin@richlegion.ru">admin@richlegion.ru</MaterialLink>
					</div>
				}
			/>
			<ListItemText
				primary={<span className={styles.font}>VK</span>}
				secondary={
					<div className={styles.font}>
						<MaterialLink href="https://vk.com/gamefree_x2" target="_blank">
							richlegion.ru
						</MaterialLink>
					</div>
				}
			/>
		</div>
	)
}

export default function Footer() {
	const translate = useTranslate({
		about: { en: 'About store', ru: 'О магазине' },
		contacts: { en: 'Contacts', ru: 'Контакты' },
		guarantee: { en: 'Guarantee', ru: 'Гарантия' },
		payment: { en: 'Shipping and payment', ru: 'Доставка и оплата' },
	})

	return (
		<div className={styles.container}>
			<div className={styles.content}>
				<div className={styles.link} style={{ color: '#3c4858', fontWeight: 500 }}>
					{translate.contacts}:
					<Contacts />
				</div>
				<div className={styles.link} style={{ color: '#3c4858' }}>
					<MaterialLink component={Link} to="/">
						{translate.about}
					</MaterialLink>
					<br />
					<MaterialLink component={Link} to="/">
						{translate.guarantee}
					</MaterialLink>
					<br />
					<MaterialLink component={Link} to="/">
						{translate.payment}
					</MaterialLink>
					<br />
					<MaterialLink className={styles.wmImage} href="https://passport.webmoney.ru/asp/certview.asp?wmid=959237439452" target="_blank">
						<img src={wmImage} />
					</MaterialLink>
				</div>
			</div>
		</div>
	)
}
